import React from 'react'
import { NavLink, withRouter } from 'react-router-dom'

const activeClassName = 'border-l border-t border-r rounded-t text-blue-700'
const activeClassNameWrapper = (page, location) => isActive(page, location) ? '-mb-px' : ''
const isActive = (page, location) => page === location

const Menu = ({ location: { pathname }}) =>
  <ul className="flex border-b">
    <li className={`mr-1 ${activeClassNameWrapper('/connections', pathname)}`}>
      <NavLink
        className="bg-white inline-block py-2 px-4 text-blue-500 hover:text-blue-800 font-semibold"
        to="/connections"
        activeClassName={activeClassName}
      >
        Connections
      </NavLink>
    </li>
    <li className={`mr-1 ${activeClassNameWrapper('/infrastructure', pathname)}`}>
      <NavLink
        className="bg-white inline-block py-2 px-4 text-blue-500 hover:text-blue-800 font-semibold"
        to="/infrastructure"
        activeClassName={activeClassName}
      >
        Infrastructure
      </NavLink>
    </li>
  </ul>

export default withRouter(Menu)
