import React from 'react'

const styles = {
  height: '90vh',
}

class Infrastructure extends React.Component {
  state = {
    url: '',
  }

  async sendData(extension, payload) {
    const response = await fetch('/api/consulize', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ extension, payload }),
    })
    const data = await response.json()
    console.log(data)
    this.setState({
      url: "https://arcentry.com/app/embed.html?id=132dab87-1757-46d7-8de1-88f87127f610"
    })
  }

  handleChange = e => {
    const [file] = e.target.files

    if (!(file.type === 'application/json' || file.name.match(/\.[0-9a-z]+$/i)[0] === '.hcl')) {
      return
    }

    const extension = file.type ? '.json' : '.hcl'

    let reader = new FileReader()
    reader.onload = () => {
      this.sendData(extension, window.btoa(reader.result))
    }

    reader.readAsText(file)
  }

  render() {
    const { url } = this.state

    return (
      <>
        {
          url
          ? <div style={styles}>
              <iframe
                width="100%"
                height="100%"
                title="Arcentry embed"
                src={url}
              />
            </div>
          : <input type="file" name="resume" onChange={this.handleChange}/>
        }
      </>
    )
  }
}

export default Infrastructure
